/* --------- ViewStudent Page------------- */

.student-parent {
  min-height: 81vh;
  /* background-color: aliceblue; */
}

.form {
  background: #fff;
  border: 4px solid blue;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  border-radius: 8px;
  padding: 1rem;
}

.form input,
textarea {
  border-radius: 8px;
  border: 2px solid blue;
  outline: none;
  color: #1d2129;
  /* margin: 1rem 0; */
  padding: 0.5rem 0.75rem;
  width: 70%;
  font-size: 1rem;
}

.student-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.studentEdit-img {
  width: 500px;
  height: 500px;
  border-radius: 50%;
}

.gender-selector {
  display: flex;
  width: 70%;
}

.gender-selector div {
  flex: 1;
}